import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import AuthTemplate from '../components/auth/AuthTemplate';
import MenuBlock from '../components/common/MenuBlock';
import Button from '../components/common/Button'
import Modal from "../components/modal/Modal";
import * as storeAction from '../_actions/store_actions'
import * as resAction from '../_actions/reserve_actions'
import styled from 'styled-components'

const StyledReserveDiv = styled.div`
  margin-bottom: 2rem;
  border-bottom: solid 2px lightgray;
  align: right;
  font-size: 1rem;

  width: 100%;

  & table {
    width: 100%;
  }

  & td {
    width: 30%;
    font-weight: bold;
  }

  & p {
    margin-bottom: 0.6rem;
  }

  & .value {
    text-align: right;
  }
`


const ResiCheckPage = () => {
  const [modalVisible, setModalVisible] = useState(false);
  const [preResContent, setPreResContent] = useState({});
  const { viewAdminReserveData } = useSelector(state => {
    return state.reserve
  })
  const openModal = () => {
    setModalVisible(true);
  };
  const closeModal = () => {
    setModalVisible(false);
  };

  const delay = 3000;

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(resAction.adminReserveView())
  }, [dispatch])

  useEffect(() => {
    function tick(){
      dispatch(resAction.adminReserveView()).then(data => {
        if(data.payload.data.length === 0) return
        
        setPreResContent(data.payload.data[0]);
      });
    }

    if(delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay])

  
  return (
    <AuthTemplate relative>
      <h3>관리자 예약 확인</h3>
      {
        viewAdminReserveData &&
        viewAdminReserveData.length > 0 &&
        viewAdminReserveData.map((data, index) =>
          <>
            <Button fullWidth style={{ marginTop: "1rem" }} onClick={openModal}>
              <p><span>{data.reservedate}</span> <span>{data.reservetime/10000}시</span> <span>{data.orderer_cust === 0 ? data.orderer_owner_nickname : data.orderer_cust_nickname}</span></p>
            </Button>
            {modalVisible && (
              <Modal
                visible={modalVisible}
                closable={true}
                maskClosable={true}
                onClose={closeModal}
              >
                <h3>예약내용</h3>
                <StyledReserveDiv>
                  <table width="100%">
                  <tr>
                    <td>결제번호:</td>
                    <td className="value"><p>{data.ordercode}</p></td>
                  </tr>
                  <tr>
                    <td>가게명:</td>
                    <td className="value"><p>{data.store_name}</p></td>
                  </tr>
                  <tr>
                    <td>예약날짜:</td>
                    <td className="value"><p>{data.reservedate}</p></td>
                  </tr>
                  <tr>
                    <td>예약시간:</td>
                    <td className="value"><p>{data.reservetime}시</p></td>
                  </tr>
                  <tr>
                    <td>예약자:</td>
                    <td className="value"><p>{data.orderer_cust === 0 ? data.orderer_owner_nickname : data.orderer_cust_nickname}</p></td>
                  </tr>
                  <tr>
                    <td>인원:</td>
                    <td className="value"><p>{!data.peoples || '' ? 1 : 2}</p></td>
                  </tr>
                  <tr>
                    <td>취소여부:</td>
                    <td className="value"><p>{data.cancel}</p></td>
                  </tr>
                  </table>
                </StyledReserveDiv>
              </Modal>
            )}
          </>
        )
      }

    </AuthTemplate>
  );
};

export default ResiCheckPage;