import styled from 'styled-components'

const ContainRadioButtonDiv = styled.div`
  width: 100%;

  input[type="radio"] {
    display: none;
  }

  input[type="radio"] + label {
    display: block;
    background: #f5f5dc;
    border: 1px solid #f5f5dc;
    padding: 0px 10px;
    text-align: center;
    font-weight: 500;
    cursor: pointer;
    color: #000;
    height: 2em;
    margin-top: 0.5em;
    margin-bottom: 0.5em;
  }

  input[type="radio"]:checked + label {
    border: 1px solid #24468A;
    background: #24468A;
    color: #fff;
  }
`

export default ContainRadioButtonDiv