import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Button from '../components/common/Button';
import AuthTemplate from '../components/auth/AuthTemplate';
import { Route, Link } from 'react-router-dom';
import AuthForm from '../components/auth/AuthForm';
import { auth, logout } from '../_actions/user_actions';
import styled from 'styled-components'

const StyledP = styled.p`
    font-weight: bold;
    font-size: 1.2rem;
    margin-top: 5vh;
    margin-bottom: 1vh;
`

const StyledDiv = styled.div`
    font-weight: bold;
    font-size: 1.1rem;

    width: 100%;

    & span {
        color: #333;
    }

    & .warp {
        float: right;
        color: lightgray;
    }
`

const MyPage = (props) => {
    const { account } = useSelector(state => {
        return state.user;
    })
    const dispatch = useDispatch()

    /* 해당 페이지가 처음 실행되었을때 실행되는 함수 */
    useEffect(() => {
        dispatch(auth());
    }, [dispatch])

    const loggedout = () => {
        dispatch(logout());
    }


    return (
        <AuthTemplate relative>
            {
                !account ?
                    <><AuthForm type="login" /></> :
                    <>
                    <hr/>
                    <Link to="/ProfilePage">
                        <StyledDiv>
                            <span>{account.account.nickname} 님</span>
                            <span className="warp"> {'>'} </span>
                        </StyledDiv>
                    </Link>
                    <hr/>
                    {
                        
                            account.admin &&
                            <>
                            <StyledP>관리자 전용</StyledP>
                                <Link to="/OwnerPage">
                                    <Button fullWidth cyan style={{ marginTop: '1rem' }}>
                                        관리자 페이지
                                    </Button>
                                </Link>
                                <br />
                                <br />
                                <hr/>
                            </>
                        }
                        <Link to="/MyResvPage">
                            <Button fullWidth cyan style={{ marginTop: '1rem' }}>
                                나의 예약 현황
                        </Button>
                        </Link><br />

                        <Link to="/PreResvPage">
                            <Button fullWidth cyan style={{ marginTop: '1rem' }}>
                                이전 예약 목록
                        </Button>
                        </Link><br />
                    </>
            }
        </AuthTemplate>
    )
}

export default MyPage;