import React, { useEffect } from "react";
import { useDispatch, useSelector } from 'react-redux'
import AuthTemplate from "../components/auth/AuthTemplate";
import styled, { css } from "styled-components";
import palette from "../lib/styles/palette";
import Button from "../components/common/Button";
import { Link } from "react-router-dom";
import * as resAction from '../_actions/reserve_actions'

const MenuBlock = styled.div`
  padding: 0.5rem 0.75rem;
  border-radius: 0.25rem;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid ${palette.gray[5]};
  width: 100%;
  margin-right: 1rem;
  ${(props) =>
    props.bold &&
    css`
      font-weight: bold;
    `}
  ${(props) =>
    props.right &&
    css`
      text-align: right;
    `}
`;

const StyledReserveDiv = styled.div`
  margin-bottom: 2rem;
  border-bottom: solid 2px lightgray;
  align: right;
  font-size: 1rem;

  width: 100%;

  & table {
    width: 100%;
  }

  & td {
    width: 30%;
    font-weight: bold;
  }

  & .value {
    text-align: right;
  }
`


const CompletePage = (props) => {

  const { reserveid } = props.match.params;
  const { indexReserveData } = useSelector(state => state.reserve);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(resAction.indexReserve(reserveid))
  }, [dispatch, reserveid])

  return (
    <>
      {indexReserveData &&
        <AuthTemplate relative>
          <h3>예약확인</h3>
          <hr/>
          <StyledReserveDiv>
            <table>
              <tr>
                <td>주문번호</td>
                <td className="value"><p>{indexReserveData.ordercode}</p></td>
              </tr>
              <tr>
                <td>가게명</td>
                <td className="value"><p>{indexReserveData.name}</p></td>
              </tr>
              <tr>
                <td>주소</td>
                <td className="value"><p>{indexReserveData.address}</p></td>
              </tr>
              <tr>
                <td>예약일시</td>
                <td className="value"><p>{indexReserveData.reservedate}</p></td>
              </tr>
              <tr>
                <td>이용시간</td>
                <td className="value"><p>{indexReserveData.reservetime / 10000}:00 ~ {indexReserveData.reservetime / 10000 + 2}:00</p></td>
              </tr>
              <tr>
                <td>예약자명</td>
                <td className="value"><p>{!indexReserveData.c_nickname || '' ?
                  indexReserveData.o_nickname :
                  indexReserveData.c_nickname
                }</p></td>
              </tr>
              <tr>
                <td>선수금 결제 금액</td>
                <td className="value">{indexReserveData.prepay}원</td>
              </tr>
            </table>
          </StyledReserveDiv>
          <Link to="/">
          <Button cyan fullWidth>
           홈으로
          </Button></Link>
        </AuthTemplate>
      }
    </>

  );
};

export default CompletePage;